import * as THREE from 'three';
import {OrbitControls} from 'three/examples/jsm/controls/OrbitControls.js';
import data from './dummy_data_3D.json'
import {GUI} from "dat.gui";

/*
GUI prototype for project "Spacewalker"
Author: Lukas Heine
 */

// const data = window.json_data;

const renderer = new THREE.WebGLRenderer({antialias: true});
renderer.setSize(window.innerWidth, window.innerHeight);
renderer.setPixelRatio( window.devicePixelRatio );
document.body.appendChild(renderer.domElement);

const layoutScale = 100;

const scene = new THREE.Scene();
const camera = new THREE.PerspectiveCamera(
    60,
    window.innerWidth / window.innerHeight,
    0.1,
    1000
);

// Sets orbit control to move the camera around
const controls = new OrbitControls(camera, renderer.domElement);

// Camera positioning
camera.position.set(layoutScale, layoutScale, layoutScale);
camera.lookAt( 0, 0, 0 );

controls.update();

const gridHelper = new THREE.GridHelper(layoutScale, layoutScale / 10);
scene.add(gridHelper);

const axesHelper = new THREE.AxesHelper(layoutScale / 2);
scene.add(axesHelper);

function render() {
    renderer.render(scene, camera);
}

renderer.setAnimationLoop(render);

window.addEventListener('resize', function() {
    camera.aspect = window.innerWidth / window.innerHeight;
    camera.updateProjectionMatrix();
    renderer.setSize(window.innerWidth, window.innerHeight);
});
const color = new THREE.Color();
let paintColor = 0xff0000;

const light = new THREE.HemisphereLight( 0xffffff, 0x888888, 3 );
light.position.set( 0, 1, 0 );
scene.add( light );

const geometry = new THREE.IcosahedronGeometry( 0.5, 3 );
const material = new THREE.MeshPhongMaterial( { color: 0xffffff } );

let mesh;
mesh = new THREE.InstancedMesh( geometry, material, data.length );

const matrix = new THREE.Matrix4();
const scale = 5
let multiSelect = false;

data.forEach(function(element, idx) {
    matrix.setPosition(element[idx].x * scale, element[idx].y * scale, element[idx].z * scale)
    mesh.setMatrixAt(idx, matrix);
    mesh.setColorAt(idx, color)
});

// data.forEach(function(element, idx) {
//         matrix.setPosition(element.x * scale, element.y * scale, element.z * scale)
//         mesh.setMatrixAt(idx, matrix);
//         mesh.setColorAt(idx, color)
//     });

mesh.receiveShadow = true;
scene.add(mesh)

// Raycast
const raycaster = new THREE.Raycaster();
const mouse = new THREE.Vector2( 1, 1 );

let selectorGeometry = new THREE.SphereGeometry(1)
let selectorMaterial = new THREE.MeshBasicMaterial( { color: 0x00ff00, opacity: 0.2, transparent: true} )
let rollOverMesh = new THREE.Mesh( selectorGeometry, selectorMaterial );
scene.add( rollOverMesh );

function onMouseMove( event ) {
    event.preventDefault();
    mouse.x = ( event.clientX / window.innerWidth ) * 2 - 1;
    mouse.y = - ( event.clientY / window.innerHeight ) * 2 + 1;
}

document.addEventListener( 'mousemove', onMouseMove );

let isMouseDown = false;
function onRMBClick( event ){
    if (event.button === 2){
        event.preventDefault();
        isMouseDown = true;
    }
}

document.addEventListener( 'mousedown', onRMBClick );

function onRMBRelease( event ){
        if (event.button === 2){
        event.preventDefault();
        isMouseDown = false;
    }
}

document.addEventListener( 'mouseup', onRMBRelease );

function animate() {
    requestAnimationFrame( animate );
    controls.update();
    raycaster.setFromCamera( mouse, camera );
    const intersection = raycaster.intersectObject( mesh );

    if ((intersection.length > 0)){
        const instanceId = intersection[0].instanceId;

        rollOverMesh.position.set(intersection[0].point.x, intersection[0].point.y, intersection[0].point.z)

        if (isMouseDown){
            if (multiSelect){
            for (let i = 0; i < mesh.count; i++) {
                const matrix = new THREE.Matrix4();
                mesh.getMatrixAt(i, matrix);

                const position = new THREE.Vector3();
                position.setFromMatrixPosition(matrix);

                if (position.distanceTo(rollOverMesh.position) < rollOverMesh.scale.x) {
                    mesh.setColorAt( i, color.setHex( paintColor ) );
                    mesh.instanceColor.needsUpdate = true;
                }
            }
            }
            else {
                mesh.setColorAt( instanceId, color.setHex( paintColor ) );
                mesh.instanceColor.needsUpdate = true;
            }
        }
    }
    render();
}

// GUI
const gui = new GUI()
const _default = {
    Cluster: 1,
    Scale: 1,
    Multiselect: false,

};
const selections = [1, 2]

gui.add(_default, 'Cluster', selections).onChange(function (newValue) {
    if (parseInt(newValue) === 1) {
        paintColor = 0xff0000;
    } else if (parseInt(newValue) === 2) {
        paintColor = 0x0000ff;
    }
});

gui.add(_default, 'Scale', 0, 100, 1).onChange(function (newValue) {
        rollOverMesh.scale.setScalar(parseInt(newValue));
})

gui.add(_default, 'Multiselect').onChange(function (newValue) {
    multiSelect = newValue;
});

animate();